@import url("./ultils/reset.css");

.root {
    --app-background: rgb();
}

.container {
    width: min(100% - 30px, 1080px);
    margin-inline: auto;
}

main {
    min-height: 100dvh;
    display: grid;
    place-items: center;
}

.star {
    cursor: pointer;
    padding: 2px;
}

.grey {
    color: #444;
    font-size: 5rem;
}

.gold {
    color: gold;
    font-size: 5rem;
}

.flex {
    display: flex;
}

h2 {
    text-align: center;
    color: #fff;
    font-size: 25px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin-top: 15px;
}
